import React from "react";
import { toast } from "react-toastify";
import db from "../../../../../libs/db";
import { MintboxFundSchema } from "../../../../../libs/schemas";
import AdminServices from "../../../../../services/api/AdminServices";
import AuthHeader from "../../../components/molecules/Headers/AuthHeader";
import MainHeader from "../../../components/molecules/Headers/MainHeader";
// import "bootstrap/dist/css/bootstrap.min.css";

export default function DashScreen(props) {
  var portfolioData = []

  const [profiled, setProfiled] = React.useState(false);
  const [loading, setloading] = React.useState(true);
  const [user, setUser] = React.useState({});
  const [portfolio, setportfolio] = React.useState([]);
  const [refreshing, setRefreshing] = React.useState(false);
  const [chartData, setChartData] = React.useState([])


  const getChartData = async (user, portfolio) => {
    var aa
    var data
    var ak = []

    if (user.profileComplete) {
      if (portfolio[0].portfolio[7].FamilyPortfolioConsolidated[0] !== undefined) {
        data = portfolio[0].portfolio[7].FamilyPortfolioConsolidated[0]
        aa = portfolio[0].portfolio[8].FamilyAssetAllocation

        aa.map((a, index) => {
          var clr = a.SchemeType === 'EQUITY' ? '#AA5F9C' : a.SchemeType === 'FOF' ? '#DCD9B1' : a.SchemeType === 'DEBT' ? '#E7AA56' : a.SchemeType === 'ELSS' ? '#5C5685' : a.SchemeType === 'CASH' ? '#5C5685' : a.SchemeType === 'GOLD' ? '#86B8B4' : '#272932'
          var dd = {
            value: Number((a.FamilyAssetBalance * 100) / data.CurrentBalance).toFixed(2),
            label: a.SchemeType,
            labelColor: '#000',
            color: clr
          }
          ak.push(dd)
        })
        await setChartData(ak)
      }
      else if (portfolio[0].portfolio[1].PortfolioConsolidated[0] !== undefined) {

        data = portfolio[0].portfolio[1].PortfolioConsolidated[0]
        aa = portfolio[0].portfolio[6].PortfolioAssetAllocation
        aa.map((a, index) => {
          var clr = a.SchemeType === 'EQUITY' ? '#AA5F9C' : a.SchemeType === 'FOF' ? '#DCD9B1' : a.SchemeType === 'DEBT' ? '#E7AA56' : a.SchemeType === 'ELSS' ? '#5C5685' : a.SchemeType === 'CASH' ? '#5C5685' : a.SchemeType === 'GOLD' ? '#86B8B4' : '#272932'
          var dd = {
            value: Number((a.PortfolioAssetBalance * 100) / data.CurrentBalance).toFixed(2),
            label: a.SchemeType,
            labelColor: '#000',
            color: clr
          }
          ak.push(dd)
        })
        await setChartData(ak)
      }
      //  aa.map((a, index) => {
      //   var clr = a.SchemeType === 'EQUITY' ? '#AA5F9C' : a.SchemeType === 'FOF' ? '#DCD9B1' : a.SchemeType === 'DEBT' ? '#E7AA56' : a.SchemeType === 'ELSS' ? '#5C5685' : a.SchemeType === 'CASH' ? '#5C5685' : a.SchemeType === 'GOLD' ? '#86B8B4' :  '#272932' 
      //   var dd = {
      //     value: title.includes("Family") ? Number((a.FamilyAssetBalance * 100)/data.CurrentBalance).toFixed(2) : Number((a.PortfolioAssetBalance * 100)/data.CurrentBalance).toFixed(2), 
      //     label: a.SchemeType, 
      //     labelColor: '#000', 
      //     color: clr
      //   }
      //   ak.push(dd)
      // })
      // console.log(ak)
      // await setChartData(ak)
    }

    // portfolio[0].portfolio[7].FamilyPortfolioConsolidated[0] === undefined
    //           ? portfolio[0].portfolio[1].PortfolioConsolidated[0] === undefined


  }


  const getData = async (user, clientData) => {
    try {
      db.get(db.model.portfolio).then((p) => {
        const aa = JSON.parse(p);
        if (p === null) {
          fetchData(user, clientData);

        } else {
          if (clientData[5].FamilyInfo.length === aa.length - 1) {
            setportfolio(aa);
            getChartData(user, aa)

            setloading(false);
            setRefreshing(false);
          } else {
            fetchData(user, clientData);

          }

        }
      });
    } catch (error) {
      toast.error("Try again ...");
    }
  };

  const getFundData = async () => {
    try {
      const datas = await new MintboxFundSchema().get();
      if (datas === []) {
        const mintboxes = await fetchFundData();
        new MintboxFundSchema().insert(mintboxes);
      }
    } catch (error) {
      toast.error("Try again ...");

    }
  };

  const getFamilyData = async (user, clientData) => {

    for (var i = 0; i < clientData[5].FamilyInfo.length; i++) {
      const res = await AdminServices.getPortfolio(clientData[5].FamilyInfo[i].MemberId, 0).then((r) => r);
      const aa = JSON.parse(res.data);
      var uData = {
        clientId: clientData[5].FamilyInfo[i].MemberId,
        portfolio: aa
      }
      portfolioData.push(uData)
    }

    db.set(db.model.portfolio, JSON.stringify(portfolioData));
    getChartData(user, portfolioData)

    setportfolio(portfolioData);
    setloading(false);
    setRefreshing(false);
  }

  const fetchData = async (user, clientData) => {
    try {
      const res = await AdminServices.getPortfolio(user.id, 0).then((r) => r);
      const aa = JSON.parse(res.data);

      var uData = {
        clientId: user.id,
        portfolio: aa
      }
      if (portfolioData.length === 0) {
        portfolioData = [uData]
      } else {
        portfolioData.push(uData)

      }

      if (clientData[5].FamilyInfo.length > 0) {
        getFamilyData(user, clientData)

      } else {
        db.set(db.model.portfolio, JSON.stringify(portfolioData));
        getChartData(user, portfolioData)

        setportfolio(portfolioData);
        setloading(false);
        setRefreshing(false);
      }



    } catch (error) {
      toast.error("Try again ...");
      setloading(false);
      setRefreshing(false);
    }
  };

  const fetchFundData = async (user) => {
    try {
      const res = await AdminServices.getFundsData().then((r) => r);
      return res.data;
    } catch (error) {
      toast.error("Try again ...");
    }
  };

  const getClientData = async (user) => {
    let da = await AdminServices.getClient(user.id).then((res) => JSON.stringify(res));
    da = JSON.parse(da);
    const aa = JSON.parse(da.data);
    db.set(db.model.appData, JSON.stringify(aa));

    getData(user, aa);

  };

  const loadData = async () => {
    try {
      db.get(db.model.user).then((p) => {
        p = JSON.parse(p);
        setUser(p.userData);
        getClientData(p.userData);
        setProfiled(p.userData.profileComplete);
        //getData(p.userData);
        // getFundData();
      });
      db.get(db.model.profileComplete);
    } catch (error) {
      toast.error("Try again ...");
    }
  };

  const refreshData = async (user) => {
    setRefreshing(true);
    db.remove(db.model.portfolio);
    loadData();
  };

  // const configurecrash = async (user) => {
  //   crashlytics().log('User signed in.');
  //   await Promise.all([crashlytics().setUserId(`${user.id}`)]);
  // };

  React.useEffect(() => {
    loadData();

    const willFocusSubscription = window.addEventListener('focus', () => {
      loadData2();
    });

    return willFocusSubscription;
  }, []);

  // useFocusEffect(
  //   React.useCallback(() => {
  //     const onBackPress = () => {
  //       // Do Whatever you want to do on back button click
  //       // Return true to stop default back navigaton
  //       // Return false to keep default back navigaton
  //       Alert.alert('Hold on!', 'Are you sure you want to go back?', [
  //         {
  //           text: 'Cancel',
  //           onPress: () => null,
  //           style: 'cancel'
  //         },
  //         { text: 'YES', onPress: () => BackHandler.exitApp() }
  //       ]);
  //       return true;
  //     };

  //     BackHandler.addEventListener(
  //       'hardwareBackPress', onBackPress
  //     );

  //     return () => BackHandler.removeEventListener(
  //       'hardwareBackPress', onBackPress
  //     );
  //   }, [])
  // );

  const loadData2 = async () => {
    const a = await localStorage.getItem('needHomeReload');
    if (a === 'yes') {
      localStorage.setItem('needHomeReload', 'no');

      loadData();
    }
  };

  const changeThemeVal = () => {
    var theme = window.localStorage.getItem("theme")
    if (theme === null || theme === undefined) {
      window.localStorage.setItem("theme", 'dark')
      document.documentElement.setAttribute("data-theme", "dark")
      document.body.style = 'background: #001516;';
    } else {
      if (theme === 'dark') {
        window.localStorage.setItem("theme", 'light')
        document.documentElement.setAttribute("data-theme", "light")
        document.body.style = 'background: #fff;';
      } else {
        window.localStorage.setItem("theme", 'dark')
        document.documentElement.setAttribute("data-theme", "dark")
        document.body.style = 'background: #001516;';
      }
    }
  }

  if (loading) {



    return (
      // <View style={{ flex: 1 }}>

      //     <View style={{ flex: 1, ...s.containerCC }}>
      //         <ActivityIndicator color={t.colors.primary} size="small" />
      //     </View>
      // </View>
      <div style={{ flex: 1 }}>
        <MainHeader header={`Hey ! 👋🏼`}
          right="bookmark-help-cart"
          screenType="Home"
          navigation={props.history} />
      </div>
    );
  } else {
    return (
      <div className="copycontainer">
        <MainHeader header={`Hey ${user.name}! 👋🏼`}
          right="bookmark-help-cart"
          screenType="Home"
          navigation={props.history} />
        <div className="containerData">

          <p onClick={() => {
            localStorage.clear("isAuthenticated")
            props.history.push("/email")
          }}>Logout</p>
          <p onClick={() => changeThemeVal()}>Change Theme</p>


        </div>
      </div>

    );
  }
}
