import React from 'react';
import { FiArrowLeft } from "react-icons/fi";
import { HiOutlineBookmark, HiOutlineHome, HiOutlineShare, HiOutlineShoppingCart } from 'react-icons/hi';
import { IoIosHelpCircleOutline } from 'react-icons/io';
import { IoLinkOutline } from 'react-icons/io5';
import { RiPencilLine, RiShareBoxLine } from 'react-icons/ri';
import colorsFinal from '../../../../../theme/colors';

const MainHeader = ({
    header,
    navigation,
    screenType,
    check,
    animatedValue,
    rightBtnFun,
    right,
    goBack
}) => {
    const [status, setStatus] = React.useState(false)

    React.useEffect(() => {
        window.onscroll = () => {
            var a = window.scrollY
            if (a > 0) {
                setStatus(true)
            } else {
                setStatus(false)
            }
        }
    }, []);
    const renderRightSide = () => {
        switch (right) {
            case 'cart':
                return (
                    <HiOutlineShoppingCart
                        className="icon-def"
                        size={24}
                        style={{ marginRight: "8px", textAlign: "right" }}
                    />
                );
            case 'help-cart':
                return (
                    <>
                        <HiOutlineHome
                            className="icon-def"
                            style={{ marginRight: "8px", textAlign: "right" }}
                            size={24}
                        />
                        <IoIosHelpCircleOutline
                            style={{ marginRight: "8px", textAlign: "right" }}
                            className="icon-def"
                            size={24}
                        />
                    </>
                );
            case 'bookmark-help-cart':
                return (
                    <>
                        <HiOutlineBookmark
                            className="icon-def"
                            size={24}
                            style={{ marginRight: "8px", textAlign: "right" }}
                        />
                        <IoIosHelpCircleOutline
                            className="icon-def"
                            size={24}
                            style={{ marginRight: "8px", textAlign: "right" }}
                        />
                        <HiOutlineShoppingCart
                            className="icon-def"
                            size={24}
                            style={{ marginRight: "8px", textAlign: "right" }}
                        />
                    </>
                );
            case 'link':
                return (
                    <IoLinkOutline
                        className="icon-def"
                        size={24}
                        style={{ marginRight: "8px", textAlign: "right" }}
                    />
                );
            case 'share':
                return (
                    <HiOutlineShare
                        className="icon-def"
                        style={{ marginRight: "8px", textAlign: "right" }}
                        size={24}
                    />
                );
            case 'upload':
                return (
                    <RiShareBoxLine
                        className="icon-def"
                        size={24}
                        style={{ marginRight: "8px", textAlign: "right" }}
                    />
                );
            case 'edit':
                return (
                    <RiPencilLine
                        className="icon-def"
                        size={24}
                        style={{ marginRight: "8px", textAlign: "right" }}
                    />
                );
            case 'share-bookmark':
                return (
                    <>
                        <HiOutlineShare
                            className="icon-def"
                            size={24}
                            style={{ marginRight: "8px", textAlign: "right" }}
                        />
                        <HiOutlineBookmark
                            className="icon-def"
                            size={24}
                            style={{ marginRight: "8px", textAlign: "right" }}
                        />
                    </>
                );
            case 'add-member':
                return (
                    <text className="textHelper" style={{ color: colorsFinal.brandPrimary }} onClick={() => {
                        navigation.navigate('Menu');
                        navigation.navigate('AddFamilyMember');
                    }} >
                        Add a member
                    </text>
                );
            case 'exit':
                return (<p onClick={() => navigation.navigate('Home')} >Exit</p>);
            case 'all-plans':
                return (<p onClick={() => navigation.navigate('Home')} >All plans</p>);
            default:
                return false;
        }
    };

    return (
        <div className={!status ? "navTop" : "navTopActive"}>
            {screenType !== 'Home' && (
                <FiArrowLeft style={{ marginRight: "10px", textAlign: "right" }} className="icon-def"
                    size={24} onClick={() => navigation.goBack()} />
            )}
            {screenType === 'FundDetails' ? (
                <div style={{
                    overflow: "hidden", display: "flex", display: "flex", flex: 1,
                    textOverflow: "ellipsis", whiteSpace: 'nowrap',
                    minWidth: 0,
                }}>
                    <span class="filename__base textHeader">{header}</span>
                    <span class="filename__base textHelper" style={{ color: colorsFinal.placeholder, marginTop: "5px" }}>{header.type}</span>
                </div>
            ) : (
                <div style={{
                    overflow: "hidden", display: "flex", display: "flex", flex: 1,
                    textOverflow: "ellipsis", whiteSpace: 'nowrap',
                    minWidth: 0,
                }}>
                    <span class="filename__base textHeader">{header}</span>
                </div>
            )}
            <div style={{ display: "flex" }}>
                {renderRightSide()}
            </div>
        </div>
    );
};

export default MainHeader;